/**
 * App模块接口列表
 */

import base from './base' // 导入接口域名列表
import axios from '@/utils/http' // 导入http中创建的axios实例

const apps = {
  appConfig (params) {
    // 获取app配置
    return axios.post(`${base.sq}/backstage/sys/app_config`, JSON.stringify(params))
  },
  configList (params) {
    // 获取渠道配置列表
    return axios.post(`${base.sq}/backstage/manage/sys_config_list`, JSON.stringify(params))
  },
  updateChannelConfig(params){
    return axios.post(`${base.sq}/backstage/manage/update_sys_config`, JSON.stringify(params))
  },
  addChannelConfig(params){
    return axios.post(`${base.sq}/backstage/sys/create_sys_config`, JSON.stringify(params))
  },
  fbSecretList(params){
    return axios.post(`${base.sq}/backstage/manage/get_fb_secret_list`, JSON.stringify(params))
  },
  updateFbSecret(params){
    return axios.post(`${base.sq}/backstage/manage/set_fb_secret`, JSON.stringify(params))
  },
  deleteFbSecret(params){
    return axios.post(`${base.sq}/backstage/manage/remove_fb_secret`, JSON.stringify(params))
  }
}

export default apps



