/**
 * Activity模块接口列表
 */

import base from './base'
import axios from '@/utils/http'
const activities = {
  turntableConfig(params) {
    // 获取转盘配置
    return axios.post(`${base.sq}/backstage/activity/get_turntable_config`, JSON.stringify(params))
  },
  setTurntableConfig(params) {
    // 获取转盘配置
    return axios.post(`${base.sq}/backstage/activity/set_turntable_config`, JSON.stringify(params))
  }
}

export default activities


