<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
export default {
  name: "app",
  components: {},
  created() {
    // 在页面加载时读取sessionStorage里的状态信息
    if (sessionStorage.getItem("store")) {
      this.$store.replaceState(
        Object.assign(
          {},
          this.$store.state,
          JSON.parse(sessionStorage.getItem("store"))
        )
      );
    }

    // 在页面刷新时将vuex里的信息保存到sessionStorage里
    window.addEventListener("beforeunload", () => {
      sessionStorage.setItem("store", JSON.stringify(this.$store.state));
    });
  }
};
</script>

<style>
html,
body,
#app {
  padding: 0px;
  margin: 0px auto;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
a {
  text-decoration: none;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
}
.allPage {
  height: 100%;
}
.form_container_header {
  padding: 1% 0 0 1%;
  margin-bottom: 4px;
}
.showDataTab {
  margin: 0 1% 0 1%;
}
.core_container {
  width: 88%;
  height: 100%;
  float: right;
  margin: auto;
  overflow-y: auto;
}
.form_container {
  width: 100%;
  box-shadow: 0px 3px 2px #eef1f6;
}

.text_common {
  color: #888;
  font-size: 13px;
}
.pageBar {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>



