import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const state = { // 要设置的全局访问的state对象
  uid: '',
  username: '',
  status: '',
  permission: '',
}
const getters = { // 实时监听state值的变化(最新状态)
  getUid (state) {
    return state.uid
  },
  getUsername (state) { // 方法名随意,主要是用来承载变化的changableNum的值
    return state.username
  },
  getStatus (state) { // 方法名随意,主要是来承载变化的showFooter的值
    return state.status
  },
  getPermission (state) { // 方法名随意,主要是来承载变化的showFooter的值
    return state.permission
  },
  getUser(state){
    return {
      uid: state.uid,
      username: state.username,
      status: state.status,
      permission: state.permission
    }
  }
}
const mutations = {
  setUid (state, uid) { // 自定义改变state初始值的方法，这里面的参数除了state之外还可以再传额外的参数(变量或对象);
    state.uid = uid
  },
  setUsername (state, username) {
    state.username = username
  },
  setStatus(state,status){
    state.status = status
  },
  setPermission(state,permission){
    state.permission = permission
  },
  setUser(state,user){
    state.uid = user.uid
    state.username = user.username
    state.status = user.status
    state.permission = user.permission
  }
}

const actions = {
  setUid ({ commit }, uid) {
    commit('setUid', uid)
  },
  setUsername ({ commit }, username) {
    commit('setUsername', username)
  },
  setStatus ({ commit }, status) {
    commit('setStatus', status)
  },
  setPermission ({ commit }, permission) {
    commit('setPermission', permission)
  },
  setUser({ commit },user){
    commit('setUser', user)
  }
}

export default { namespaced: true, state, getters, mutations, actions }



