import base from './base'
import axios from '@/utils/http'

const user = {
    list(params) {
        return axios.post(`${base.sq}/user/list`, JSON.stringify(params))
    },
    register(params) {
        return axios.post(`${base.sq}/user/register`, JSON.stringify(params))
    },
    update(params) {
        return axios.post(`${base.sq}/user/update`, JSON.stringify(params))
    },
    login(params) {
        return axios.post(`${base.sq}/user/login`, JSON.stringify(params))
    },
    loginout(params) {
        return axios.post(`${base.sq}/user/loginout`, JSON.stringify(params))
    },
    delete(params) {
        return axios.post(`${base.sq}/user/delete`, JSON.stringify(params))
    }
}

export default user



