/**
 * game模块接口列表
 */

import base from './base' // 导入接口域名列表
import axios from '@/utils/http' // 导入http中创建的axios实例

const games = {
  // gameList () {
  //   return axios.get(`${base.sq}/topics`)
  // },
  // getGameById (id, params) {
  //   return axios.get(`${base.sq}/topic/${id}`, {
  //     params: params
  //   })
  // },
  nodeList(params) {
    // 获取节点列表
    return axios.post(`${base.sq}/backstage/game/node_list`, JSON.stringify(params))
  },
  getNodeById(params){
    return axios.post(`${base.sq}/backstage/game/get_node_info`, JSON.stringify(params))
  },
  addNode(params) {
    //新增节点
    return axios.post(`${base.sq}/backstage/game/create_node`, JSON.stringify(params))
  },
  updateNode(params) {
    //更新节点
    return axios.post(`${base.sq}/backstage/game/update_node`, JSON.stringify(params))
  },
  deleteNode(params) {
    //删除节点
    return axios.post(`${base.sq}/backstage/game/remove_node`, JSON.stringify(params))
  },
  gameList(params) { // 游戏列表
    return axios.post(`${base.sq}/backstage/game/game_list`, JSON.stringify(params))
  },
  getGameById(params) { // 游戏新增
    return axios.post(`${base.sq}/backstage/game/get_game_info`, JSON.stringify(params))
  },
  addGame(params) { // 游戏新增
    return axios.post(`${base.sq}/backstage/game/create_game`, JSON.stringify(params))
  },
  updateGame(params) { // 游戏新增
    return axios.post(`${base.sq}/backstage/game/update_game`, JSON.stringify(params))
  },
  getRoomById(params) { 
    return axios.post(`${base.sq}/backstage/game/get_room_info`, JSON.stringify(params))
  },
  addRoom(params) { // 游戏房间新增
    return axios.post(`${base.sq}/backstage/game/create_room`, JSON.stringify(params))
  },
  updateGameAdminStatus(params) { 
    return axios.post(`${base.sq}/backstage/game/update_game_admin_status`, JSON.stringify(params))
  },
  updateGameStatus(params) { 
    return axios.post(`${base.sq}/backstage/game/update_game_status`, JSON.stringify(params))
  },
  roomList(params) { // 获取房间列表
    return axios.post(`${base.sq}/backstage/game/room_list`, JSON.stringify(params))
  },
  updateRoom(params) { // 游戏房间修改
    return axios.post(`${base.sq}/backstage/game/update_room`, JSON.stringify(params))
  },
  updateRoomAdminStatus(params) { 
    return axios.post(`${base.sq}/backstage/game/update_room_admin_status`, JSON.stringify(params))
  },
  // updateRoomAdminStatus(params) { 
  //   return axios.post(`${base.sq}/backstage/game/update_room_admin_status`, JSON.stringify(params))
  // },
  updateRoomStatus(params) { 
    return axios.post(`${base.sq}/backstage/game/update_room_status`, JSON.stringify(params))
  },
  pushGame(params) { // 发布游戏
    return axios.post(`${base.sq}/backstage/game/publish_game`, JSON.stringify(params))
  },
  pushRoom(params) { // 发布房间
    return axios.post(`${base.sq}/backstage/game/publish_room`, JSON.stringify(params))
  },
}

export default games



