<template>
  <div class="header_container">
    <div class="inner_container">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item v-for="(item, index) in $route.meta" :key="index">
          <span
            v-if="index == $route.meta.length - 1 || item.path === '#'"
            class="no-redirect"
            >{{ item.title }}</span
          >
          <a v-else v-bind:href="item.path">{{ item.title }}</a>
          <!--<router-link v-else :to="{ name:item }">{{ item }}</router-link>-->
        </el-breadcrumb-item>
      </el-breadcrumb>

      <div class="right-bar">
        <div class="demo-fit">
          <div class="block" v-for="fit in fits" :key="fit">
            <el-avatar
              shape="square"
              :size="40"
              :fit="fit"
              :src="url"
            ></el-avatar>
          </div>
        </div>
        <div class="text_common">
          <span v-text="user.username"></span>
        </div>
        <el-dropdown class="text_common">
          <el-button type="primary" plain size="small">
            更多菜单
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="a">用户详情</el-dropdown-item>
            <el-dropdown-item @click.native="loginout()">退出</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    handleCommand(command) {
      this.$message("click on item " + command);
    },
    loginout() {
      let that = this;
      this.$api.user
        .loginout({ username: this.$store.getters["user/getUsername"] })
        .then(res => {
          if (res.data.code === 200) {
            that.$store.dispatch("user/setUser", {
              uid: null,
              username: null,
              status: null,
              permission: null
            });
            that.$router.push("/login");
          } else {
            this.$message({
              showClose: true,
              message: "操作失败",
              type: "error"
            });
          }
        });
    }
  },
  data() {
    return {
      // username: this.$store.state.name,
      user:this.$store.getters["user/getUser"],
      fits: ["fill"],
      url:
        "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg"
    };
  }
};
</script>

<style lang="less">
.header_container {
  background-color: white !important;
  height: 60px;
  //display: flex;
  justify-content: space-between;
  //align-items: center;
  width: 88%;
  float: right;
  box-shadow: 0px 3px 2px #eef1f6;
}
.inner_container {
  width: 100%;
  height: 100%;
  margin: 5px 20px;
  /*flex 布局*/
  display: flex;
  /*实现垂直居中*/
  align-items: center;
  justify-content: space-between;
}
.no-redirect {
  color: #97a8be;
  cursor: text;
}
.el-dropdown-link {
  cursor: pointer;
  color: #409eff;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.right-bar {
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  width: 15%;
}
.text_common {
  margin: auto 16px;
  color: #888;
  font-size: 13px;
}
.log_style {
  font-size: 20px;
  font-weight: bold;
  color: #888;
}
</style>



