<template>
  <div class="home">
    <div class="manage_page fillcontain">
      <el-row style="height:100%;">
        <el-col
          :span="24"
          style="min-height: 100%;background-color: #324157;overflow: hidden;"
        >
          <el-menu
            router
            :default-active="this.$route.path"
            class="el-menu-vertical-demo"
            @open="handleOpen"
            @close="handleClose"
            background-color="#324157"
            text-color="#bfcbd9"
            active-text-color="#ffd04b"
            :unique-opened="true"
          >
            <el-menu-item index="home"
              ><i class="el-icon-s-home log_style"></i>Dashboard</el-menu-item
            >
            <el-submenu
              v-for="(item, i) in leftMenuList"
              :key="i"
              v-show="item.second"
              :index="item.path"
            >
              <template slot="title">
                <i :class="[item.logo + ' log_style']"></i>
                {{ item.title }}
              </template>
              <el-menu-item
                class="noClickText"
                v-for="(sec, j) in item.second"
                :key="j"
                :index="sec.path"
                >{{ sec.title }}</el-menu-item
              >
            </el-submenu>
          </el-menu>
        </el-col>
        <!-- <el-col :span="12">
          <keep-alive>
            <router-view></router-view>
          </keep-alive>
        </el-col> -->
      </el-row>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: "Home",
  computed: {
    // defaultActive: function () {
    //   // /!*刷新后导航还是高亮显示且展开*!/ //或者直接$route.name
    //   // console.log(this.$route.path) /home/list  只取list就行 和el-menu-item的index对应
    //   return this.$route.path.replace('/home/', '')
    // }
  },
  data() {
    return {
      leftMenuList: [
        {
          title: "App配置",
          path: "*",
          logo: "el-icon-s-operation",
          second: [
            // { title: "获取app配置", path: "/sys/appConfig" },
            // { title: "获取支付渠道列表", path: "/sys/payList" },
            // { title: "获取商品信息", path: "/shop/goodsInfo" },
            { title: "渠道配置列表", path: "/sys/channelConfig" },
            { title: "FB密钥列表", path: "/sys/fbSecret" }
          ]
        },
        {
          title: "游戏管理",
          path: "game",
          logo: "el-icon-s-data",
          second: [
            { title: "节点列表", path: "/game/nodeList" },
            { title: "游戏列表", path: "/game/gameList" },
            { title: "房间列表", path: "/game/roomList" }
          ]
        },
        {
          title: "邮件管理",
          path: "mail",
          logo: "el-icon-message",
          second: [{ title: "发送邮件", path: "/mail/sendMail" }]
        },
        {
          title: "活动管理",
          path: "activity",
          logo: "el-icon-orange",
          second: [{ title: "转盘配置", path: "/activity/turntableConfig" }]
        },
        {
          title: "用户管理",
          path: "user",
          logo: "el-icon-s-custom",
          second: [{ title: "用户列表", path: "/user/list" }]
        }
      ]
    };
  },
  mounted() {},
  methods: {
    handleOpen(key, keyPath) {
      //console.log("open :" + key + "," + keyPath);
    },
    handleClose(key, keyPath) {
      //console.log("close :" + key + "," + keyPath);
    }
  }
};
</script>
<style lang="less" scoped>
.home {
  height: 100%;
  width: 12%;
  float: left;
}
.fillcontain {
  height: 100%;
}
.el-menu {
  border: 0;
}
.el-col {
  background-color: #eff2f7;
  height: 100%;
  overflow: auto;
}
.home {
  /deep/ .el-submenu .el-menu-item {
    background-color: #1f2d3d !important;
  }
  /deep/ .el-submenu__title:hover,
  .el-submenu .el-menu-item:hover {
    background-color: #48576a !important;
  }
}
.log_style {
  font-size: 20px;
  font-weight: bold;
  color: white;
}
.noClickText {
  moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;
}
</style>



