/**
 * mail模块接口列表
 */

import base from './base' // 导入接口域名列表
import axios from '@/utils/http' // 导入http中创建的axios实例

const mail = {
  sendMail (params) { // 发送邮件
    return axios.post(`${base.sq}/backstage/mail/send_mail`, JSON.stringify(params))
  }
}

export default mail



